.main {
  width: 340px;
  margin: 0 auto;
}
.t{
  width: 300px;
  height: 50px;
  line-height: 50px;
  padding: 2px 20px;
  font-size: 16px;
  display: flex;
  justify-content: space-between;
}
.input{
  display: flex;
  justify-content: space-between;
  padding: 2px 20px;
  margin-top: 20px;
}
.Total{
    width: 300px;
  height: 50px;
  line-height: 50px;
  padding: 2px 20px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
  border: 1px solid rgba(0, 0, 0, 0.125);
  background-color: #ffffff;
}

/* ============================================ */


.header h2 {
  width: 302px;
  height: 50px;
  line-height: 50px;
  padding: 2px 20px;
  margin: 0;
  text-align: center;
  color: #fff;
  background-color: #eb076a;
}

.noti {
  width: 300px;
  height: 50px;
  line-height: 50px;
  padding: 2px 20px;
  font-size: 16px;
  border: 1px solid rgba(0, 0, 0, 0.125);
}
.noti .notiNBR {
  font-size: 18px;
  margin-right: 4px;
}

.Activites {
  width: 100%;
  height: 223px;
}

.Check {
  background-color: #007bffd0;
  border-color: #007bff;
  color: #fff;
}

.noCheck,
.Check {
  width: 300px;
  height: 50px;
  line-height: 50px;
  padding: 2px 20px;
  display: flex;
  border: 1px solid rgba(0, 0, 0, 0.125);
  justify-content: space-between;
